* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.Variable {
    padding: 0;
    margin: 0;
    font-family: 'Roboto', sans-serif;
    background-color: #000000;
    color: white;
    width: 21%;
    display: flex;
    justify-content: space-between;
    cursor: move;
}

.Variable-header {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.Variable-content {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.dot {
    height: 25px;
    width: 25px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
}

.d {
    display: flex;
    justify-content: center;
    align-items: center;
}